<template>
  <!--首页轮播图-->
    <div>
      <div class="block">
        <el-carousel trigger="click" height="300px">
          <el-carousel-item v-for="item in img_list" :key="item">
            <img class="img-spread" :src="item"/>
          </el-carousel-item>
        </el-carousel>
      </div>
    </div>
</template>

<script>
    export default {
        name: "carousel",
        data () {
          return {
            img_list: [
              "static/img/current/banner02.png",
              "static/img/current/banner02.png"
            ],
          }
        },
    }
</script>

<style scoped>
  .block{
    border-radius:6px;
    overflow: hidden;
  }

  .el-carousel__item h3 {
    color: #475669;
    font-size: 14px;
    opacity: 0.75;
    line-height: 150px;
    margin: 0;
  }

  .el-carousel__item:nth-child(2n) {
    background-color: #99a9bf;
  }

  .el-carousel__item:nth-child(2n+1) {
    position: relative;
  }
</style>



