<template>
      <div class="headerWrapper">
        <header class="header">
          <el-row :gutter="20">
            <el-col :span="6" :offset="2">
              <img @click="goindex()" class="nav-img" :src=logo />
            </el-col>
            <el-col :span="14">
              <ul class="container-right">
                <li v-for="(item,sort) in list"
                    :key="sort" @click="active(sort)"
                    :class="{active:index == sort}">{{item}}</li>
                </ul>
                </el-col>
              </el-row>
            </header>
          </div>
    </template>

    <script>
        export default {
            name: "topGuide",
            data () {
              return {
                logo:"static/img/current/logo.svg",
                list: ['博客','码云','前端开发','UI设计','关于&留言'],
                index:0
              }
            },
            methods:{
              active:function(sort){
                this.index = sort;
              },
              goindex(){
                this.$router.push({ path:"/"});
              }
            }
        }
    </script>

    <style scoped>
    .active{
      position: relative;
      color: #303133!important;
    }
    .active:after {
      content: "";
      width: 100%;
      height: 2px;
      background: #409EFF;
      position: absolute;
      left: 0%;
      bottom: 0;
      -webkit-transition: all .8s;
      transition: all .8s;
    }
    </style>



