<template>
  <div class="content">
    <div class="feed_new_tit">
      <span class="line"></span><span class="txt">热门文章</span>
    </div>
    <div>
        <ul class="list-row">
          <li v-for="(item,idx) in list" :key="idx">
            <div class="list-img">
              <img class="img-spread" src="https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1600444336465&di=f43845559b91823c51a4120e9e883c88&imgtype=0&src=http%3A%2F%2Fimg.sccnn.com%2Fbimg%2F337%2F45079.jpg">
            </div>
            <div class="listtxt">
              <div class="listtitle">YzmCMS短标签及模板语法</div>
              <div class="litstime">2020-08-30</div>
              <div class="browse">45646次阅读</div>
            </div>
          </li>
        </ul>
    </div>
  </div>
</template>

<script>
    export default {
        name: "popular",
        data () {
          return {
            list:3,
          }
        },
    }
</script>

<style scoped lang="scss">
  .list-img{
    position: relative;
    overflow: hidden;
    width: 70px;
    height: 70px;
    background-color: $MainAsh;
    border-radius: 6px;
    float: left;
  }
  .listtxt{
    margin-left: 80px;
  }
  .listtitle{
    width: 100%;
    float: left;
    font-weight: bold;
    font-size: 14px;
    margin-top: 7px;
    height: 14px;
    line-height: 14px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }
  .content{
    float: left;
    width: -webkit-fill-available;
  }
  .litstime{
    float: left;
    font-size: 13px;
    color: $MainAsh;
    width: 100%;
    height: 13px;
    line-height: 13px;
    margin-top: 10px;
  }
  .browse{
    float: left;
    font-size: 13px;
    color: $MainAsh;
    width: 100%;
    height: 13px;
    line-height: 13px;
    margin-top: 4px;
  }
  .list-row{
    margin-top: -15px;
  }
  .list-row li{
    cursor: pointer;
    float: left;
    margin-top: 15px;
    width: 100%;
  }
  .list-row li:hover .listtitle{
    color: $MainBlue;
  }
</style>



