<template>
  <div class="content">
    <div class="feed_new_tit">
      <span class="line"></span><span class="txt">搜索文章</span>
    </div>
    <div>
      <el-form @submit.native.prevent class="input-group" style="display: block;">
        <el-input v-model="key"  @keyup.enter.native="addAnswer" placeholder="输入文章标题搜索" suffix-icon="el-icon-search"></el-input>
      </el-form>
    </div>
  </div>
</template>

<script>
    export default {
        name: "search",
        data () {
          return {
            key:''
          }
        },
        methods:{
          addAnswer(){
              if (this.key == ""){
                this.$message({
                  type: 'info',
                  message: '输入内容不得为空'
                });
              }else {
                this.$router.push({ path:"/index/searchResults",query:{searchtxt:this.key} });
                this.$emit("onceAgain");  //执行搜索结果页面传的problem函数
              }
          }
        }
    }
</script>

<style scoped>

</style>



