<template>
    <div>
      <div class="cover">
        <div class="avatar">
          <img class="img-spread" :src=avatarImg>
        </div>
        <!--不想成为黑客的程序员不是好产品经理-->
        <div class="cover-tite">Mike's个人博客</div>
        <el-row class="covertop">
          <el-col :span="12">
            <div @click="gitee" class="coversvgRow">
              <svg class="icon coversvg" aria-hidden="true">
                <use xlink:href="#icongitee"></use>
              </svg>
              <span>Gitee</span>
            </div>
          </el-col>
          <el-col :span="12">
            <div class="coversvgRow">
              <svg class="icon coversvg" aria-hidden="true">
                <use xlink:href="#iconyouxiang"></use>
              </svg>
              <a target="_blank" href="http://mail.qq.com/cgi-bin/qm_share?t=qm_mailme&email=4dDS1tnY0tTQ2NmhkJDPgo6M" style="text-decoration:none;">
                <span>发邮件</span>
              </a>
            </div>
          </el-col>
        </el-row>
      </div>
    </div>
</template>

<script>
    export default {
        name: "personalCover",
        data () {
          return {
            avatarImg:"https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1600418653415&di=5bf54140600618d7af992b3527e6b62e&imgtype=0&src=http%3A%2F%2Fdp.gtimg.cn%2Fdiscuzpic%2F0%2Fdiscuz_x5_gamebbs_qq_com_forum_201306_19_1256219xc797y90heepdbh.jpg%2F0"
          }
        },
        methods:{
          gitee(){
            window.open("https://gitee.com/liaoquanzhi");
          }
        }
    }
</script>

<style scoped lang="scss">
  .cover{
    height: 300px;
    background-color: $MasterWhite;
    border-radius:6px;
    -webkit-box-shadow: 0 1px 2px 0 rgba(0,0,0,0.04);
    box-shadow: 0 1px 2px 0 rgba(0,0,0,0.04);
    border-top: 1px solid $MasterWhite;
    padding: 0 30px;
  }
  .avatar{
    position: relative;
    overflow: hidden;
    left: 50%;
    margin-left: -65px;
    margin-top: 40px;
    width: 130px;
    height: 130px;
    background-color: $MainAsh;
    border-radius:100px;
  }
  .covertop{
    margin-top: 30px;
  }

  .el-col {
    text-align:center;
    height: 30px;
  }
  .el-col span{
    line-height: 30px;
    height: 30px;
    float: left;
    margin-left: 10px;
    color: $MainAsh;
  }
  .coversvgRow:hover span{
    color: $MainBlack;
  }
  .coversvg{
    float: left;
    height: 30px;
    width: 30px;
  }
  .coversvgRow{
    display:inline-block;
    cursor: pointer;
  }
  .cover-tite{
    text-align: center;
    margin-top: 20px;
    font-weight: bold;
  }
</style>



