import Vue from 'vue'
import Router from 'vue-router'
import index from '@/components/index/index'
import error from '@/components/error/error'
import essay from '@/components/index/essay'
import searchResults from '@/components/index/searchResults'


// 修复在本页面跳转本页面报错
const originalPush = Router.prototype.push
Router.prototype.push = function push(location) {
  return originalPush.call(this, location).catch(err => err)
}

Vue.use(Router)

export default new Router({
  routes: [
    {path: '/',name: 'index',component: index,meta:{title: '首页'}},
    {path: '/index/essay',name: 'essay',component: essay,meta:{title: '文章详情'}},
    {path: '/index/searchResults',name: 'searchResults',component: searchResults,meta:{title: '搜索结果'}},
    {path: '*',name: 'error',component: error,meta:{title: '404'}},
  ]
})



