<template>
  <div class="content">
    <div class="feed_new_tit">
      <span class="line"></span><span class="txt">文章归档（最近一年）</span>
    </div>
    <div>
      <div class="timelist" v-for="(item,index) in timelist" :key="index">
        <div class="headimg" :style="getRandomColor()">
          <div>C</div>
        </div>
        <div class="tit">2020年0{{index}}月</div>
        <div class="quantity">{{index}}篇</div>
      </div>
    </div>
  </div>
</template>

<script>
    export default {
        name: "releaseTime",
        data () {
          return {
            timelist:5,
          }
        },
        mounted() {

        },
        methods:{
          getRandom(min, max) {
              return Math.floor(Math.random() * (max - min + 1)) + min;
            },
          // 返回随机颜色
          getRandomColor() {
            let c1 = this.getRandom(0, 254);
            let c2 = this.getRandom(0, 254);
            let c3 = this.getRandom(0, 254);
            let c4 = 'background-color:rgb(' + c1 + ',' + c2 + ',' + c3 + ')';
            return c4
            }
        }
    }
</script>

<style scoped lang="scss">
  .content{
    float: left;
    padding: 0 15px 15px 15px;
  }
  .feed_new_tit{
    margin: 15px 0 0 0;
  }
  .timelist{
    cursor: pointer;
    font-size: 14px;
    float: left;
    width: 100%;
    margin-top: 15px;
  }
  .timelist:hover{
    color: $MainBlue;
  }
  .quantity{
    float: right;
    color: $MainAsh;
  }
  .headimg{
    position: relative;
    float: left;
    overflow: hidden;
    width: 32px;
    height: 32px;
    background-color: #2c3033;
    border-radius: 4px;
    text-align: center;
    line-height: 32px;
    color: $MasterWhite;
  }
  .headimg div{
    -webkit-transform: skew(-10deg); /* for Chrome||Safari */
    -ms-transform: skew(-10deg); /* for IE */
    -moz-transform:skew(-10deg);/* for Firefox */
    -o-transform:skew(-10deg);/* for Opera */
  }
  .tit{
    float: left;
    line-height: 32px;
    margin-left: 10px;
  }
</style>



