<template>
  <div>
    <top-guide></top-guide>
    <div class="aRow">
      <el-row :gutter="20">
        <el-col :span="14" :offset="2">
          <!--正文-->
          <article-details :psMsg=index></article-details>
        </el-col>
        <el-col :span="6">
          <!--搜索-->
          <search></search>
        </el-col>
        <el-col :span="6">
          <!--标签-->
          <label-list></label-list>
        </el-col>
      </el-row>
    </div>
  </div>
</template>

<script>
    import TopGuide from "../common/topGuide";
    import PersonalCover from "../common/personalCover";
    import Carousel from "../common/carousel";
    import Search from "../common/search";
    import LabelList from "../common/LabelList";
    import ArticleDetails from "../common/articleDetails";
    export default {
        name: "essay",
        components: {ArticleDetails, LabelList, Search, Carousel, PersonalCover, TopGuide},
        data(){
          return{
            index:''
          }
        },
        mounted() {
          this.problem();
        },
        methods:{
          problem(){
            this.index = this.$route.query.details;
          }
        }
    }
</script>

<style scoped>

</style>



