<template>
  <div>
    <section class="app-main">
      <div data-v-5eb3c775="" class="wscn-http404-container">
        <div class="wscn-http404">
          <div class="pic-404">
            <img draggable="false" src="/static/img/error/02.png" alt="404" class="pic-404__parent"></div>
          <div class="bullshit">
            <div class="bullshit__oops">
              <font>
                <font>糟糕！</font></font>
            </div>
            <div class="bullshit__headline">
              <font>
                <font>网站管理员说您无法进入此页面...</font></font>
            </div>
            <div class="bullshit__info">
              <font>
                <font>请检查您输入的URL是否正确，或单击下面的按钮返回首页。</font></font>
            </div>
            <a href="/" class="bullshit__return-home">
              <font>
                <font style="vertical-align: inherit;">回到首页</font></font>
            </a>
          </div>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
export default {
  name: "error"
}
</script>

<style scoped>
.wscn-http404-container{
  -webkit-transform: translate(-50%,-50%);
  transform: translate(-50%,-50%);
  position: absolute;
  top: 45%;
  left: 50%;
}
.wscn-http404 .pic-404{
  position: relative;
  float: left;
  width: 600px;
  overflow: hidden;
}
.wscn-http404{
  position: relative;
  width: 1200px;
  padding: 0 50px;
  overflow: hidden;
}
.wscn-http404 .bullshit{
  position: relative;
  float: left;
  width: 300px;
  padding: 60px 0;
  overflow: hidden;
}
.wscn-http404 .pic-404__parent{
  width: 100%;
}
.wscn-http404 .bullshit__headline, .wscn-http404 .bullshit__oops{
  font-weight: 700;
}
.wscn-http404 .bullshit__oops{
  font-size: 32px;
  line-height: 40px;
  color: #1482f0;
  margin-bottom: 20px;
}
.wscn-http404 .bullshit__info{
  font-size: 13px;
  line-height: 21px;
  color: grey;
  margin-bottom: 30px;
}
.wscn-http404 .bullshit__headline{
  font-size: 20px;
  line-height: 24px;
  color: #222;
  margin-bottom: 10px;
}
.wscn-http404 .bullshit__return-home{
  display: block;
  float: left;
  width: 110px;
  height: 36px;
  background: #1482f0;
  border-radius: 100px;
  text-align: center;
  color: #fff;
  font-size: 14px;
  line-height: 36px;
  cursor: pointer;
}
</style>



