<template>
  <div class="content">
    <div class="feed_new_tit">
      <span class="line"></span><span class="txt">标签</span>
    </div>
    <div>
      <div class="Labellist">
        <el-tag  v-for="(item,idx) in list" :key="idx" type="info">
          {{item}}
        </el-tag>
      </div>
    </div>
  </div>
</template>

<script>
    export default {
        name: "LabelList",
        data () {
          return {
            list:[
              'css',
              'html',
              'JavaScript',
              'vue',
              'uni-app',
              'bootstrap',
              'element-ui'
            ]
          }
        },
    }
</script>

<style scoped>
  .Labellist{
    margin-top: -10px;
    margin-left: -10px;
  }
  .Labellist span{
    margin-top: 10px;
    margin-left: 10px;
    cursor: pointer;
  }

  .el-tag{
    padding: 0 15px;
  }
</style>



